import { _decorator, Component, Node, tween, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GridGenerator')
export class GridGenerator extends Component {

    @property(Node) dummyCandy : Node = null!;

    start() {
        this.setNodeActive(this.dummyCandy, true);
        this.decreaseScale(this.dummyCandy,4);
    }

    update(deltaTime: number) {
        
    }

    private setNodeActive(settingNode : Node , isActive : boolean){
        settingNode.active = isActive;
    }


    async decreaseScale(target:Node, duration : number) : Promise<void> {
        return new Promise<void>((resolve) =>{
            tween(target)
            .to(duration , {scale: Vec3.ZERO})
            .call(()=> resolve())
            .start();
        })
    }

}

